.footer-box {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    display: flex;
    flex-flow: row;
    height: 100px;

    .icons-box {
         display: flex;
         margin: auto;

         .link-image-icon {
              margin: auto;
              padding: 0 6px;

              img.image-icon {
                   width: auto;
                   height: 50px;
                   opacity: 0.5;
                   transition: 0.5s;
              }
         }
    }
}